.Item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ccc;
  padding: 10px;
}

.Item-left {
  flex: 1;
}

.Item-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Item-image {
  width: 64px;
  height: 64px;
  border: 1px solid #ccc;
  margin-right: 10px;
  float: left;
}

.Item-title {
  font-weight: bold;
  margin-bottom: 0.4em;
}

.Item-price {
  text-align: right;
  font-size: 14px;
  color: #555;
}


